import React, { Component } from 'react';
import { Card } from 'antd';
import LeftFolder from './leftFolder';
import RightTable from './RightTable';
import styles from './index.module.less';
import SystemTabs from '../common/systemTabs';

export default class workFlowManagement extends Component {
    constructor(props){
        super(props);
        this.state = {
            folderId:"",//当前选择的文件夹id
        }
    }
    updateSelectFolder = (folderId) => {
        this.setState({folderId})
    }
    render() {
        return (
            <React.Fragment>
                <SystemTabs activeTab="workFlowManagement"/>
                <div style={{padding:20}}>
                    <Card title={false} bodyStyle={{padding:0}}>
                        <div className={styles.container}>
                            <LeftFolder updateSelectFolder={this.updateSelectFolder} OutFolderId={this.state.folderId}/>
                            <RightTable folderId={this.state.folderId} updateSelectFolder={this.updateSelectFolder}/>
                        </div>
                    </Card>
                </div>
            </React.Fragment>
            
        )
    }
}
