import React, { Component } from 'react'
import {Input, Button, Select, Table, message, Modal, TreeSelect,Switch } from 'antd'
import { withRouter } from "react-router";
import moment from 'moment';
import { getAction,postAction } from '@/axios';
import {hasButtonPermission} from '@/util'
import StepComponent from './stepComponent';
const { Option } = Select;
const { TreeNode } = TreeSelect;

class RightTable extends Component {
    state={
        folderId:"",//所属文件夹id(查询用)
        flowName: '', // 流程名称(查询用)
        flowType: '', // 流程类型（查询用）
        flowInfo:{},//当前编辑的流程基础信息
        newFlowName: '', // 新建流程名称
        newFlowType: '1', // 新建流程类型
        selectedDoc: '', // 新建流程时， 选择的文件夹
        mode:"add",//当前弹窗模式，新增/编辑
        columns:[
            {
                title: '序号',
                dataIndex: 'sortIndex',
                width: '60px',
            },{
                title: '流程名称',
                dataIndex:'processName',
                render: (text,record) => (<span onClick={()=>{this._showDetailModal(record)}} className='operation-link' title={text}> {text} </span>)
            },{
                title: '创建人',
                dataIndex: 'name',
                render: (text) => (<span title = {text}> {text} </span>)
            },{
                title: "流程类型",
                dataIndex: 'processType',
                render: (text) => {
                    if (parseInt(text)  === 1) {
                        return (<span title = '创建计划'> 创建计划 </span>)
                    } else if (parseInt(text)  === 2) {
                        return (<span title = '新增表单'> 新增表单 </span>)
                    } else if (parseInt(text)  === 3) {
                        return (<span title = '填报内容'> 填报内容 </span>)
                    }
                }
            },{
                title: '修改时间',
                dataIndex:'updateTime',
                render: (text) => {
                    let str = moment.unix(text/1000).format("YYYY/MM/DD hh:mm:ss")
                    return  (<span title = {str}> {str} </span>)
                }
            },{
                title: '所属文件夹',
                dataIndex: 'folderName',
                render:(text)=>(<span title={text}>{text}</span>)
            },{
                title:'状态',
                dataIndex:'status',
                render:(text,record) => {
                    return hasButtonPermission('workFlow007')?(<Switch checkedChildren="启用" unCheckedChildren="禁用" checked={ text===1 ? true : false} onChange={(val) =>{this._handleToggle(val,record)}} />):null
                }
            },{
                title: '操作',
                dataIndex: 'handle',
                width: '150px',
                render: (text, record) => {
                    return (
                        <span>
                            {//状态码为1，启用状态，不可修改
                                record.status===1?null:(
                                    <React.Fragment>
                                        {
                                            hasButtonPermission('workFlow005') && <React.Fragment>
                                                <span className="operation-link" onClick={this.flowChange.bind(this, record)}>修改</span>
                                                <span style={{margin: "0px 15px"}}>|</span>
                                            </React.Fragment>
                                        }
                                        
                                    </React.Fragment>
                                )
                            }
                            {
                                hasButtonPermission('workFlow006') && 
                                <span className="operation-link" onClick={()=>{this.flowDelete(record)}}>删除</span>
                            }
                        </span>
                    )
                }
            }
        ],
        data: [
            // {processId:"",processName:"流程名称",name:"创建人",processType:"1",//流程类型updateTime:123445,//修改时间folderName:"所属文件夹"}
        ],
        
        pagination:{
            current:1,
            pageSize: 10,
            showTotal: (total) => (`共${total}条数据`),
            total:null
        },
        buildFlow:false,
        buildFlowTitle:'新建流程',
        treeData: [],// 新建流程时， 选择的文件夹数据
        showDetailModal:false,
        stepsData:null,
    }
    componentDidMount(){
        // this.getTreeData();
        this.getTableData();
        document.addEventListener("keydown",this._onkeydown);
    }
    _onkeydown = (e) => {
        if(e.keyCode === 13){
            this.getTableData();
        }
    }
    componentWillUnmount(){
        document.removeEventListener("keydown",this._onkeydown)
    }
    //请求流程详情并显示
    _showDetailModal = (record) => {
        getAction(`/ybt/wf/process/viewProcess?processId=${record.processId}`).then(res=>{
            if(res.success){
                let stepsData = {current:res.obj.current,steps:res.obj.setps}
                this.setState({
                    showDetailModal:true,
                    stepsData
                });
            }else{
                message.error(res.obj)
            }
        })
    }
    componentDidUpdate(preProps){
        if(preProps.folderId!==this.props.folderId){
            this.setState({
                selectedDoc:this.props.folderId
            },()=>{this.getTableData()})
        }
    }
    //获取树的结构
    getTreeData = () => {
        getAction("/ybt/wf/process/queryFolderTree").then(res => {
            if(res.success){
                this.setState({
                    treeData:res.obj || []
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //渲染树
    renderTreeNodes = data => data.map(item => {
        if (item.children) {
        return (
            <TreeNode title={item.title} key={item.id+"key"} value={item.id}>
                {this.renderTreeNodes(item.children)}
            </TreeNode>
        );
        }
        return <TreeNode key={item.id+"key"} title={item.title} value={item.id}/>;
    });

    getTableData =(page) => {
        let params = {
            processName:this.state.flowName,
            processType:this.state.flowType,
            processFolderId:this.props.folderId
        }
        let pageSize = this.state.pagination.pageSize;
        let current = 1;
        if(page==="page"){
            current = this.state.pagination.current;
        }

        postAction(`/ybt/wf/process/queryFolderProcessList?pageNum=${current}&pageSize=${pageSize}`,params).then(res => {
            if(res.success){
                this.setState({
                    data:res.obj.rows.map((item,index) => ({sortIndex:index+1,...item})),
                    pagination:{
                        current:res.obj.page,
                        pageSize:res.obj.pageSize,
                        total:res.obj.total,
                        showTotal: (total) => (`共${total}条数据`),
                    }
                })
            }
        })
    }
    //重置
    handleReset=()=>{
        //修改父组件的folderId
        this.props.updateSelectFolder('');
        //重置分页
        let pagination = this.state.pagination;
        pagination.current = 1;
        pagination.pageSize = 10;

        this.setState({ 
            flowName:'',
            flowType:'',
            pagination
        },()=>{
            this.getTableData();
        });
    }

    // 流程名称输入框--查询条件
    flowNameChange = (e) => {
        this.setState({ flowName:e.target.value })
    }
    // 流程类型变更--查询条件
    flowTypeChange = (value) => {
        this.setState({ flowType:value })
    }
    //分页请求
    handleTableChange = (pagination) => {
        this.setState({
            pagination:{
                ...this.state.pagination,
                current:pagination.current,
                pageSize: pagination.pageSize
            }
        },()=>{this.getTableData("page")})
    }
    //切换工作流程管理状态
    _handleToggle = (val,record) => {
        const status = val?1:2;
        let params = {
            processId:record.processId,
            processType:record.processType,
            status
        }
        postAction(`/ybt/wf/process/updatePrccessStatus`,params).then(res => {
            if(res.success){
                message.success("修改成功");
                this.getTableData()
            }else{
                message.error(res.obj)
            }
        })
    }

    // 流程删除
    flowDelete = (record) => {
        Modal.confirm({
            title: '流程删除确认',
            content: '确定删除该流程吗？',
            okText: '确认',
            okType: 'danger',
            cancelText: '取消',
            onOk:()=> {
                this.deleteFlowRequest(record.processId)
            }
        })
    }
    deleteFlowRequest = (flowId) => {
        getAction(`/ybt/wf/process/deleteProcessById?processId=${flowId}`).then(res => {
            if(res.success){
                message.success("删除成功");
                this.getTableData();
            }else{
                message.error(res.obj)
            }
        })
    }
    // 流程修改，跳转至流程修改界面
    flowChange = (record) => {
        getAction(`/ybt/wf/process/queryProcessTaskIsExist?processId=${record.processId}`).then(res => {
            if(res.success){
                if(res.obj.flag){//如果返回状态码为可修改，则直接调整，否则；给出后端提示文字
                    this.getTreeData();
                    this.setState({
                        buildFlow: true,
                        buildFlowTitle:'修改流程',
                        mode:"edit",
                        newFlowName:record.processName,//初始化流程名称
                        newFlowType:record.processType+"",//初始化流程类型
                        selectedDoc:record.processFolderId,//初始化流程所属文件夹
                        // selectedDoc:"689e64c7c3c311e98152005056b227b6",
                        flowInfo:record,
                    })
                }else{
                    message.warn(res.obj.msg)
                }
            }else{
                message.error(res.obj)
            }
        })
        
    }
    // 新建流程
    buildNewFlow = () =>{
        this.getTreeData();
        this.setState({ buildFlow: true,buildFlowTitle:'新建流程',mode:"add",newFlowName:"",newFlowType:"1",selectedDoc:"" })
    }
    // 隐藏新建流程弹出框
    hideBuildFlow = () => {
        this.setState({ buildFlow:false })
    }
    // 确认新建流程
    buildFlowOk = () => {
        if (!this.state.newFlowName) {
            message.warn('请输入新建流程名称！！！')
            return  false
        } else if (!this.state.newFlowType) {
            message.warn('请选择新建流程类型')
            return false
        } else if (!this.state.selectedDoc) {
            message.warn('请选择所属文件夹！！！')
            return false
        }
        if(this.state.mode==="add"){//新增
            window.localStorage.setItem(
                "flowInfo",
                JSON.stringify({
                    type:this.state.newFlowType,
                    flowName:this.state.newFlowName,
                    folderId:this.state.selectedDoc
                })
            );
            this.props.history.push(`/workFlowDesign`)
        }else{//编辑（带id）
            window.localStorage.setItem(
                "flowInfo",
                JSON.stringify({
                    type:this.state.newFlowType,
                    flowName:this.state.newFlowName,
                    folderId:this.state.selectedDoc,
                    id:this.state.flowInfo.processId
                })
            );
            this.props.history.push(`/workFlowDesign`)
        }
        this.hideBuildFlow()
    }
    // 新建流程名称输入框
    newFlowNameChange = (e) => {
        this.setState({
            newFlowName: e.target.value
        })
    }
    // 新建流程类型下拉框
    newFlowTypeChange = (value) => {
        this.setState({
            newFlowType: value
        })
    }
    // 新建流程时， 选择文件夹下拉选择
    selectedDocChange = value => {
        this.setState({
            selectedDoc:value
        })
    }

    render() {
        return (
            <div style={{padding: '20px',overflow:"auto"}}>
                <div>
                    <span style={{float: 'left',width: "100px", lineHeight: "32px"}}>流程名称:</span>
                    <Input style={{width: '300px', float: 'left', marginRight: "20px"}} value={this.state.flowName} onChange={this.flowNameChange} placeholder='请输入流程名称' />
                    <span style={{float:'left',width: "100px", lineHeight: "32px"}}>流程类型:</span>
                    <Select value={this.state.flowType} style={{ width: 120, float: 'left', marginRight: '20px' }} onChange={this.flowTypeChange}>
                        <Option value="">全部</Option>
                        <Option value="1">创建计划</Option>
                        <Option value="2">新增表单</Option>
                        <Option value="3">填报内容</Option>
                    </Select>
                    <Button type='primary' ghost icon="search" onClick={this.getTableData.bind(this)}> 搜索 </Button>
                    <Button icon="sync" style={{marginLeft:12}} onClick={this.handleReset}>重置</Button>
                </div>
                {
                    hasButtonPermission('workFlow004') && 
                <Button type='primary' icon="plus" style={{margin: '20px 0px'}} onClick={this.buildNewFlow}> 新增流程 </Button>

                }
                <Table
                    rowKey="sortIndex"
                    columns={this.state.columns}
                    dataSource={this.state.data}
                    pagination={this.state.pagination}
                    onChange={this.handleTableChange}
                />
                <Modal
                    title={this.state.buildFlowTitle}
                    visible={this.state.buildFlow}
                    onOk={this.buildFlowOk}
                    onCancel={this.hideBuildFlow}
                    okText = '确定'
                    cancelText = '取消'
                >
                    <div style={{marginBottom: '10px'}}>
                        <span style={{width: '100px', float:'left', lineHeight: '32px'}}>流程名称：</span>
                        <Input style={{width: '300px'}} value={this.state.newFlowName} onChange={this.newFlowNameChange} placeholder='请输入新建流程名称...' />
                    </div>

                    <div style={{marginBottom: '10px'}}>
                        <span style={{width: '100px', float:'left', lineHeight: '32px'}}>流程类型：</span>
                        <Select value={this.state.newFlowType} style={{ width: 300 }} onChange={this.newFlowTypeChange} disabled={this.state.mode==="edit"}>
                            <Option value="1">创建计划</Option>
                            <Option value="2">新增表单</Option>
                            <Option value="3">填报内容</Option>
                        </Select>
                    </div>

                    <div style={{marginBottom: '10px'}}>
                        <span style={{width: '100px', float:'left', lineHeight: '32px'}}>所属文件夹：</span>
                        <TreeSelect
                            style={{ width: '300px' }}
                            value={this.state.selectedDoc}
                            dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                            // treeData={this.state.treeData}
                            placeholder="请选择所属文件夹"
                            treeDefaultExpandAll
                            onChange={this.selectedDocChange}
                            // disabled={this.state.mode==="edit"}
                        >
                            {this.renderTreeNodes(this.state.treeData)}
                        </TreeSelect>
                    </div>
                </Modal>
                <Modal
                    visible={this.state.showDetailModal}
                    bodyStyle={{overflow:'auto'}}
                    title={'流程详情'}
                    width={800}
                    destroyOnClose={true}
                    footer={false}
                    onCancel={()=>{this.setState({showDetailModal:false,stepData:[]})}}
                >
                    <StepComponent data={this.state.stepsData}/>
                </Modal>
            </div>
        )
    }
}

export default withRouter(RightTable)
